<template>
  <div id="TemplateUploadManagement">
    <el-menu
      class="leftNav"
      unique-opened
      style="width: 12vw; height: 95vh; position: fixed; left: 0"
    >
      <el-submenu
        :index="index + ''"
        v-for="(item, index) in leftNavData"
        :key="index"
      >
        <template slot="title">{{ index }}</template>
        <el-submenu
          :index="index2 + ''"
          v-for="(item2, index2) in item"
          :key="index2"
        >
          <template slot="title">{{ index2 }}</template>
          <el-menu-item
            :index="index2 + item3"
            v-for="(item3, index3) in item2"
            :key="index3"
            @click="NavClick(index, index2, item3)"
            >{{ item3 }}</el-menu-item
          >
        </el-submenu>
      </el-submenu>
    </el-menu>
    <header style="color: white; margin-left: -1.5vw; font-weight: 800">
      <el-button type="primary" @click="openUpload()">上 传</el-button>
      <el-button type="primary" @click="openEdit()">编 辑</el-button>
      <el-button type="danger" @click="deleteEmail()">删 除</el-button>
      <el-button type="warning" @click="returMai()">所 有</el-button>
      <span style="padding-left: 25vw; font-size: 1.5rem">上传邮件模板</span>
    </header>
    <footer>
      <table class="tempalateUploadTable">
        <thead>
          <tr>
            <th></th>
            <th>序号</th>
            <th>渠道</th>
            <th>对应国家</th>
            <th>语言</th>
            <th>售后类型</th>
            <th>适用情况</th>
            <th>邮件回复模板</th>
            <th>上传人</th>
            <th>上传时间</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in tableList" :key="index">
            <td><input type="checkbox" :value="item.id" class="checkli" /></td>
            <td @click="tagClick(index)">{{ index + 1 + (page - 1) * 50 }}</td>
            <td @click="tagClick(index)">{{ item.platform }}</td>
            <td @click="tagClick(index)">{{ item.country }}</td>
            <td @click="tagClick(index)">{{ item.language }}</td>

            <td @click="tagClick(index)">{{ item.problem_type }}</td>
            <td @click="tagClick(index)">{{ item.Instructions_use }}</td>

            <td @click="tagClick(index)">
              <div class="tdDiv">{{ item.email_content }}</div>
            </td>
            <td @click="tagClick(index)">{{ item.upload_people }}</td>
            <td @click="tagClick(index)">{{ item.upload_time }}</td>
          </tr>
        </tbody>
      </table>

      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="page"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pagetotle"
        style="margin: 1vh 0 0 44vw"
      >
      </el-pagination>
    </footer>
    <!-- ELE弹框 -->
    <el-dialog
      title="上传邮件模板文件"
      :visible.sync="centerDialogVisible"
      :close-on-click-modal="false"
      width="42%"
      center
    >
      <span>
        <div class="mainTop">
          <div style="position: relative">
            <span style="position: absolute; top: 1.2vh; left: 0vw"
              >国家：</span
            >
            <span style="position: absolute; top: 6vh; left: -1.4vw"
              >问题类型：</span
            >
            <span style="position: absolute; top: 6vh">语言：</span>
            <span
              >渠道：
              <el-select v-model="uploadPlatform" placeholder="请选择">
                <el-option
                  v-for="item in uploadPlatformList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </span>
            <span style="margin-left: 1.5vw">
              <el-col :span="25">
                <el-autocomplete
                  class="inline-input"
                  v-model="uploadCountry"
                  :fetch-suggestions="querySearch"
                  placeholder="请输入内容"
                  style="margin: 0 1.7vw 0 2.5vw; width: 11.2vw"
                ></el-autocomplete>
              </el-col>
            </span>
          </div>
          <div>
            <span>
              <el-col :span="25">
                <el-autocomplete
                  class="inline-input"
                  v-model="uploadProblemType"
                  :fetch-suggestions="querySearch2"
                  placeholder="请输入内容"
                  style="margin: 0 1.7vw 0 2.5vw; width: 11.2vw"
                ></el-autocomplete>
              </el-col>
            </span>
            <span>
              <el-col :span="25">
                <el-autocomplete
                  class="inline-input"
                  v-model="uploadLanguage"
                  :fetch-suggestions="querySearch3"
                  placeholder="请输入内容"
                  style="margin: 0 1.7vw 0 2.5vw; width: 11.2vw"
                ></el-autocomplete>
              </el-col>
            </span>
          </div>
          <div>
            <span class="textareaspan">请输入模板：（直接输入文字）</span>
            <textarea
              name=""
              id=""
              cols="50"
              v-model="uploadTextarea"
              style="
                font-size: 0.9rem;
                line-height: 2.2vh;
                height: 20vh;
                resize: none;
              "
            ></textarea>
            <br />
            <span>适用情况(直接输入文字)</span><br />
            <textarea
              name=""
              id=""
              cols="50"
              v-model="Instructions_use"
              style="
                font-size: 0.9rem;
                line-height: 2.2vh;
                height: 20vh;
                resize: none;
                margin-top:0;
              "
            ></textarea>
          </div>
        </div>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="warning" @click="upLoadSet()">重 置</el-button>
        <el-button type="primary" @click="addSubmit()">提 交</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="编辑"
      :visible.sync="editDialogVisable"
      width="20%"
      :before-close="handleClose"
    >
      <el-form
        ref="templateForm"
        :model="editTemplateForm"
        label-width="100px"
        :inline="true"
      >
        <el-form-item label="售后类型" prop="problem_type">
          <el-input
            type="textarea"
            :rows="2"
            v-model="editTemplateForm.problem_type"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="邮件回复模板" prop="email_content">
          <el-input
            type="textarea"
            :rows="5"
            v-model="editTemplateForm.email_content"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="handleClose">取 消</el-button>
          <el-button type="primary" @click="editTemplateSubmit"
            >确 定</el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      editTemplateForm: {
        problem_type: "",
        email_content: ""
      },
      editDialogVisable: false,
      editTemplateFormData: "",
      centerDialogVisible: false,
      leftNavData: [], //左边导航栏
      tableList: [], //表格数据
      uploadPlatformList: [], //上传文件平台下拉框
      restaurants: [], //上传文件国家下拉框
      restaurants2: [], //上传文件问题类型下拉框
      restaurants3: [], //上传文件语言下拉框
      uploadPlatform: "", //上传文件平台值
      uploadCountry: "", //上传文件国家值
      uploadLanguage: "", //上传文件语言值
      uploadProblemType: "", //上传文件问题类型值
      uploadTextarea: "", //上传文件模板值
      total: 0, //页码当前页
      page: 1, //
      firstNav: "",
      twiceNav: "",
      threeNav: "",
      Instructions_use: "" // 适用情况
    };
  },
  methods: {
    editTemplateSubmit() {
      if (
        this.editTemplateForm.problem_type === "" &&
        this.editTemplateForm.email_content === ""
      ) {
        alert("请至少填写其中一个！");
        return;
      }
      this.editDialogVisable = false;
      let url = this._url2 + "server/customer_management/email_template_manage";
      this.editTemplateFormData.append(
        "problem_type",
        this.editTemplateForm.problem_type
      );
      this.editTemplateFormData.append(
        "email_content",
        this.editTemplateForm.email_content
      );
      let upload_time = this.formatTime();
      this.editTemplateFormData.append("upload_time", upload_time);
      this.$axios.put(url, this.editTemplateFormData).then(res => {
        if (res.data.code === 200) {
          alert("编辑成功！");
          // 刷新数据
          this.getDefaultData();
        }
      });
    },
    formatTime() {
      let time = new Date();
      let year = time.getFullYear();
      let month =
        time.getMonth() + 1 > 10
          ? time.getMonth() + 1
          : "0" + (time.getMonth() + 1);
      let day = time.getDate() > 10 ? time.getDate() : "0" + time.getDate();
      return year + "-" + month + "-" + day;
    },
    handleClose() {
      this.editDialogVisable = false;
      // 清空

      this.$refs["templateForm"].resetFields();
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    querySearch2(queryString, cb) {
      var restaurants2 = this.restaurants2;
      var results = queryString
        ? restaurants2.filter(this.createFilter(queryString))
        : restaurants2;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant2 => {
        return (
          restaurant2.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    querySearch3(queryString, cb) {
      var restaurants3 = this.restaurants3;
      var results = queryString
        ? restaurants3.filter(this.createFilter(queryString))
        : restaurants3;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant3 => {
        return (
          restaurant3.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    getDefaultData() {
      //获取默认数据
      axios({
        url: this._url2 + "server/customer_management/email_template_manage",
        method: "get",
        params: {
          page: this.page
        }
      }).then(res => {
        console.log(res);
        // this.leftNavData = res.data.left_data_dict;
        this.tableList = res.data.data;
        this.restaurants = res.data.country_data;
        this.restaurants2 = res.data.problem_data;
        this.restaurants3 = res.data.language_data;
        this.uploadPlatformList = res.data.channel_data;
        this.total = res.data.count[0].count / 2;
      });
    },
    NavClick(index, index2, item3) {
      //获取点击的表格数据
      this.firstNav = index;
      this.twiceNav = index2;
      this.threeNav = item3;
      axios({
        url: this._url2 + "server/customer_management/email_template_manage",
        method: "get",
        params: {
          platform: index,
          country: index2,
          language: item3,
          page: 1
        }
      }).then(res => {
        console.log(res);
        this.tableList = res.data.data;
        this.$nextTick(() => {
          $(".tempalateUploadTable>tbody").scrollTop(0);
        });
        this.total = res.data.count[0].count / 2;
        this.page = 1;
      });
    },
    openEdit() {
      //删除选中的数据
      let demo = [];
      let checkli = document.querySelectorAll(".checkli");
      for (let i = 0; i < checkli.length; i++) {
        if (checkli[i].checked == true) {
          demo.push(checkli[i].value);
        }
      }
      if (demo.length !== 1) {
        alert("请选中一个数据进行编辑！");
      } else {
        this.editDialogVisable = true;
        console.log(demo[0]);
        let item = this.tableList.find(i => {
          return i.id == demo[0];
        });
        console.log(item);
        this.editTemplateForm.problem_type = item.problem_type || "";
        this.editTemplateForm.email_content = item.email_content || "";

        this.editTemplateFormData = new FormData();
        this.editTemplateFormData.append("id", demo[0]);
      }
    },
    openUpload() {
      //打开上传编辑框
      this.centerDialogVisible = true;
      this.uploadProblemType = "";
      this.uploadTextarea = "";
      this.uploadLanguage = "";
      this.uploadCountry = "";
      this.uploadPlatform = "";
      this.Instructions_use = "";
    },
    upLoadSet() {
      //重置上传编辑框
      this.uploadProblemType = "";
      this.uploadTextarea = "";
      this.uploadLanguage = "";
      this.uploadCountry = "";
      this.Instructions_use = "";
      this.uploadPlatform = "";
    },
    addSubmit() {
      //新增上传
      let checkcountry = this.checkeChinese(this.uploadCountry);
      let checkLanguage = this.checkeChinese(this.uploadLanguage);
      let checkType = this.checkeChinese(this.uploadProblemType);
      if (
        this.uploadCountry == "" ||
        this.uploadPlatform == "" ||
        this.uploadLanguage == "" ||
        this.uploadProblemType == "" ||
        this.uploadTextarea == "" ||
        this.Instructions_use == "" ||
        checkcountry == false ||
        checkLanguage == false ||
        checkType == false
      ) {
        alert("请把所有选项填上（问题类型、国家、语言必须为中文字段）");
      } else {
        this.centerDialogVisible = false;
        let date = new Date();
        let Y = date.getFullYear(); //获取完整的年份(4位)
        let M = date.getMonth() + 1; //获取当前月份(0-11,0代表1月)
        let D = date.getDate(); //获取当前日(1-31)
        let timers = Y + "-" + M + "-" + D;
        let formData = new FormData();
        formData.append("country", this.uploadCountry);
        formData.append("platform", this.uploadPlatform);
        formData.append("language", this.uploadLanguage);
        formData.append("problem_type", this.uploadProblemType);
        formData.append("email_content", this.uploadTextarea);
        formData.append("upload_time", timers);
        formData.append("Instructions_use", this.Instructions_use);
        formData.append("upload_people", "上传人2");
        this.$axios
          .post(
            this._url2 + "server/customer_management/email_template_manage",
            formData
          )
          .then(res => {
            if (res.data.code == 200) {
              alert("新增成功");
              axios({
                url:
                  this._url2 +
                  "server/customer_management/email_template_manage",
                method: "get",
                params: {
                  platform: this.firstNav,
                  country: this.twiceNav,
                  language: this.threeNav,
                  page: this.page
                }
              }).then(res => {
                console.log(res);
                this.tableList = res.data.data;
              });
            }
          });
      }
    },
    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      this.page = newPage;
      axios({
        url: this._url2 + "server/customer_management/email_template_manage",
        method: "get",
        params: {
          platform: this.firstNav,
          country: this.twiceNav,
          language: this.threeNav,
          page: this.page
        }
      }).then(res => {
        console.log(res);
        this.tableList = res.data.data;
        this.$nextTick(() => {
          $(".tempalateUploadTable>tbody").scrollTop(0);
        });

        let checkli = document.querySelectorAll(".checkli");
        for (let i = 0; i < checkli.length; i++) {
          checkli[i].checked = false;
        }
      });
    },
    returMai() {
      //按钮路由跳转
      this.$router.push("/MailTemplateManagement");
    },
    deleteEmail() {
      //删除选中的数据
      let demo = [];
      let checkli = document.querySelectorAll(".checkli");
      for (let i = 0; i < checkli.length; i++) {
        if (checkli[i].checked == true) {
          demo.push(checkli[i].value);
        }
      }
      if (demo.length == 0) {
        alert("请选中想删除的数据");
      } else {
        this.$confirm("此操作将永久删除选中模板, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$axios
              .delete(
                this._url2 + "server/customer_management/email_template_manage",
                {
                  params: { id: demo }
                }
              )
              .then(res => {
                if (res.data.code == 200) {
                  this.$message({
                    type: "success",
                    message: "删除成功!"
                  });
                  let checkli = document.querySelectorAll(".checkli");
                  for (let i = 0; i < checkli.length; i++) {
                    checkli[i].checked = false;
                  }
                  axios({
                    url:
                      this._url2 +
                      "server/customer_management/email_template_manage",
                    method: "get",
                    params: {
                      platform: this.firstNav,
                      country: this.twiceNav,
                      language: this.threeNav,
                      page: this.page
                    }
                  }).then(res => {
                    this.tableList = res.data.data;
                  });
                }
              });
          })
          .catch(() => {});
      }
    },
    tagClick(index) {
      //点击选中
      if (document.querySelectorAll(".checkli")[index].checked == true) {
        document.querySelectorAll(".checkli")[index].checked = false;
      } else {
        document.querySelectorAll(".checkli")[index].checked = true;
      }
    },
    checkeChinese(str) {
      //中文正则\
      let demo = str.match(/[^\u4e00-\u9fa5]/g);
      if (demo) {
        return false;
      } else {
        return true;
      }
    }
  },
  created() {
    this.getDefaultData();
    axios({
      //左侧导航栏（权限用）
      url: this._url2 + "server/personnel_management/area_sign2/",
      method: "get"
    }).then(res => {
      console.log(res, 5);
      this.leftNavData = res.data.data1;
    });
  }
};
</script>
<style>
#TemplateUploadManagement .el-textarea__inner {
  resize: none;
}
#TemplateUploadManagement .el-form {
  text-align: center;
}
</style>
<style scoped>
table {
  /*width: 84vw;*/
  /* margin: 10px 50px; */
  border-collapse: collapse;
  margin-left: 13vw;
}

tbody {
  display: block;
  max-height: 73vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 8vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 40px;
  line-height: 40px;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 40px;
  line-height: 40px;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 5px !important;
}

header {
  margin: auto;
  padding: 2vh;
  text-indent: 13.5vw;
}

.mainTop {
  height: 50vh;
  width: 100%;
  margin-left: 6vw;
}

.mainTop div {
  height: 5vh;
}

.mainTop div:nth-child(3) {
  position: relative;
}

.mainTop select {
  width: 15vw;
  height: 4vh;
}

.mainTop textarea {
  margin-top: 2vh;
  height: 11vh;
  width: 29vw;
  font-size: 0.7rem;
}

.textareaspan {
  position: absolute;
}

table tr td:nth-child(6),
table tr th:nth-child(6) {
  width: 10vw;
}

.tdDiv {
  max-height: 10vh;
  overflow: hidden;
  text-overflow: ellipsis;
  overflow-y: scroll;
  line-height: 20px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dddddd;
  background-clip: padding-box;
  min-height: 5vh;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #bbb;
}

table tr th:nth-child(1),
table tr td:nth-child(1),
table tr th:nth-child(2),
table tr td:nth-child(2),
table tr th:nth-child(3),
table tr td:nth-child(3),
table tr th:nth-child(5),
table tr td:nth-child(5) {
  width: 3vw;
}
table tr th:nth-child(4),
table tr td:nth-child(4) {
  width: 4vw;
}
table tr th:nth-child(8),
table tr td:nth-child(8) {
  width: 43.5vw;
}
#TemplateUploadManagement .el-dialog__footer {
  padding: 35px 20px 20px;
}
</style>



